import React from 'react';
import styled from 'styled-components';
import store from '../../store';
import Nav from 'react-bootstrap/Nav'
import '../../App.css';
import { Tab, Col, Row, Popover, OverlayTrigger, Button } from 'react-bootstrap';
import moment from 'moment';
import { Link } from 'react-router-dom';
import StarRatingComponent from 'react-star-rating-component';

const Wrapper = styled.div`
  margin-left:100px;
  margin-right:100px;
  padding-top: 20px;
  padding-left: 10px;
  font-family: 'Nanum Gothic', sans-serif;
  min-width:1190px;
`;
  // background: linear-gradient(135deg , #eaeaea, #c5cae9 )

const MainTitle = styled.div`
  text-align: center;
  width: 400px;
  padding: 20px 10px 20px 10px;
  font-size: 15px;
  font-family: 'Nanum Gothic', sans-serif;
  font-weight: 700;
  margin-left: auto;
  margin-right: auto;

`;


const SubTitle = styled.div`
  // width: 500px;
  padding-left: 30px;
  font-size: 25px;
  font-weight: bold;
`;

const LeftLayout = styled.div`
  margin: 30px 0px 0px 50px;
  font-size: 16px;
  width: 65%;
  height: 200px;
  color: black;
  font-weight:400;
`;

const SideBySide1 = styled.div`
  height: 150px;
  margin-left:-20px;
`;

const SideBySide2 = styled.div`
  // float: right;
  // width: 500px;
  margin-left :-20px;
  height: 150px;
  background: ;
  margin-top: 0;
`;

const SideBySide3 = styled.div`
  float: left;
  width: 500px;
  height: 150px;
  background: ;
  margin-left: -20px;
`;

const NicknameLayout = styled.div`
  padding: 75px 20px 20px 20px;
  height: 350px;
  width: 90%;
  
  font-weight: 600;
  
  background: ;
  border-radius: 3px;
  border-top: 2px solid gray;
  text-decoration: none;
  background: white;
  text-align: left;
  margin-left: 0;
  // margin-right: auto;
`;

const PasswordLayout = styled.div`
  padding: 72px 20px 20px 20px;
  height: 350px;
  width: 90%;
  
  font-weight: 600;
  
  // cursor: pointer;
  border-radius: 3px;
  border-top: 2px solid gray;
  text-decoration: none;
  background: white;
  text-align: left;
  margin-left: 0;
  // margin-right: auto;
  
`;

const MyReviewLayout = styled.div`
  padding: 72px 20px 20px 20px;
  height: auto;
  width: 90%;
  margin-bottom:20px;

  font-weight: 600;
  
  /* cursor: pointer; */
  border-radius: 3px;
  border-top: 2px solid gray;
  border-bottom: 2px solid gray;
  text-decoration: none;
  background: white;
  text-align: left;
  margin-left: 0;
  // margin-right: auto;
`;

// const Table = styled.div`
//   border: 1px solid black;
//   border-radius:2px;
//   width: 85%;
//   height: auto;
//   padding: 5px 10px 5px 10px;
//   margin: 0 auto;
// `;

const Table = styled.div`
  border-bottom : 1px solid black;
  width: 85%
  height: auto;
  padding: 20px 10px 15px 10px;
  margin: 0;

`;
const Review = styled.div`
  padding-left:40px;
`;
const Rate = styled.div`
  font-size: 14px;
`;

const Title = styled.div`
  font-size: 18px;
`

const Info = styled.div`
  font-size: 12px;
  text-align: right;
`;

const Contents = styled.div`
  font-size: 14px;
`;

const DeleteLayout = styled.div`
  padding: 100px 20px 20px 20px;
  height: 400px;
  width: 90%;
  margin-bottom:20px;
  font-weight: 600;
  color: #6B66FF;
  /* cursor: pointer;*/
  border-radius: 3px;
  border-top: 2px solid gray;
  border-bottom: 2px solid gray;
  text-decoration: none;
  background: white;
  text-align: left;
  margin-left: 0;
  // margin-right: auto;

`;

const AllInput = styled.input`

  font-size: 15px;
  box-sizing: border-box;
  width: 260px;
  height: 40px;
  margin: 0px 5px 0px 20px;
  border: 1px solid black;
  border-radius: 3px;
  transition: border 0.1s ease-in-out;
  outline: none;
  &:hover,
  &:focus {
    border: 2px solid #6B66FF;
  }
`;

const AllButton = styled.button`
  font-family: 'Nanum Gothic', sans-serif;
  font-size: 16px;
  font-weight: 600;
  width: 60px;
  height: auto;
  color: #6B66FF;
  border: 1px solid #6B66FF;
  background-color: lightgray;
  padding: 0.5rem;
  padding-bottom: 0.4rem;
  cursor: pointer;
  border-radius: 3px;
  text-decoration: none;
  transition: .2s all;

  &:hover {
    background: #6B66FF;
    color: white;
  }
`;

const WarningButton = styled.button`
  font-family: 'Nanum Gothic', sans-serif;
  font-size: 16px;
  font-weight: 600;
  width: 60px;
  height: auto;
  // color: #6B66FF;
  color:white;
  border: 1px solid #6B66FF;
  background-color: #6B66FF;
  padding: 0.5rem;
  padding-bottom: 0.4rem;
  cursor: pointer;
  border-radius: 3px;
  text-decoration: none;
  transition: .2s all;

  &:hover {
    background: #ff6b66;
    color: white;
  }
`;

const HJLayout = styled.div`
  padding: 75px 20px 20px 20px;
  height: 350px;
  width: 90%;
  
  font-weight: 600;
  
  background: ;
  border-radius: 3px;
  border-top: 2px solid gray;
  text-decoration: none;
  background: white;
  text-align: left;
  margin-left: 0;
  // margin-right: auto;
  
`;
const MyTab = styled.div`
    font-size: 20px;
`;
const AllThing = styled.div`
  
`;
//background: linear-gradient(135deg , #eaeaea, #c5cae9 );

const Pointword = styled.div`
  font-size:18px;
  font-weight:bold;
`;

const ExitPopover = styled(Popover)`
  text-align: center;
`

const MyPagePresenter = ({takeNewNickname, submitNewNickname, takeOldPassword, takeNewPassword, submitNewPassword, takeWithdrawPassword, submitWithdraw, myReviews}) => {
  const user = store.getState().user;

  const popover = (
    <ExitPopover id="popover-basic">
      <ExitPopover.Body>
           ,    .<br/>
        <br/>
        <b> ?</b><br/>
        <br/>
        <Button onClick={submitWithdraw}></Button>
      </ExitPopover.Body>
    </ExitPopover>
    );

  return (
    <AllThing>
    <Wrapper>        
        <MainTitle><h1></h1></MainTitle>
        <Tab.Container id="left-tabs-example" defaultActiveKey="first">
          <Row>
            <Col sm={3}>
              <Nav variant="pills" className="flex-column">
                <MyTab>
                  <Nav.Item>
                    <Nav.Link eventKey="first"> </Nav.Link>
                  </Nav.Item>
                  <Nav.Item>
                    <Nav.Link eventKey="second">  </Nav.Link>
                  </Nav.Item>
                </MyTab>
              </Nav>
            </Col>
            <Col sm={9}>
              <Tab.Content>
                <Tab.Pane eventkey="zero">
                  <HJLayout>

                  </HJLayout>
                </Tab.Pane>
                <Tab.Pane eventKey="first">
                  <NicknameLayout>
                    <SubTitle><p> </p></SubTitle>
                      <LeftLayout>
                        <Pointword><p> </p></Pointword>
                        <p>  : {user.nickname}</p>
                      <SideBySide1>
                        <AllInput onChange={takeNewNickname} placeholder="  "></AllInput>
                        <AllButton button onClick={submitNewNickname}></AllButton>
                      </SideBySide1>
                      </LeftLayout>
                    </NicknameLayout>
                    
                    <PasswordLayout>
                    <SubTitle><p> </p></SubTitle>
                      <LeftLayout>
                        {/* <p> </p> */}
                      <SideBySide2>
                        <AllInput input type='password' onChange={takeOldPassword} placeholder="  "></AllInput><br/>
                        <AllInput input type='password' onChange={takeNewPassword} placeholder="  "></AllInput>
                        <AllButton button onClick={submitNewPassword}></AllButton>
                      </SideBySide2>
                      </LeftLayout>
                    </PasswordLayout>
                    <DeleteLayout>
                      <SubTitle><p> </p></SubTitle>
                        <LeftLayout>
                            <p>    ({user.user_id})    .</p>
                            
                            <SideBySide3>
                              <AllInput input type='password' onChange={takeWithdrawPassword} placeholder="  "></AllInput>
                              <OverlayTrigger trigger="click" placement="top" overlay={popover}>
                                <WarningButton variant="success"></WarningButton>
                              </OverlayTrigger>
                            </SideBySide3>
                        </LeftLayout>
                      </DeleteLayout>

                </Tab.Pane>
                <Tab.Pane eventKey="second">
                  <MyReviewLayout>
                    <SubTitle><p>  </p></SubTitle>
                    {!(myReviews.length === 0) ? (<>
                      {myReviews.slice(0).reverse().map((review) => ( 
                        <Review>
                          <Table key={review.review_id}>
                            <Title>
                              <Link to={`/Detail?code=${review.movieCd}`}>{review.movieTitle}</Link><br/> 
                            </Title>
                            <Rate>
                              <StarRatingComponent 
                                name="rate2" 
                                editing={false}
                                starCount={5}
                                value={review.rate}
                              />
                            </Rate>
                            <Contents>
                              {review.contents}<br/>
                            </Contents>
                            <Info>
                              <i>{moment(review.created).format('YYYY-MM-DD')} </i>
                            </Info>
                          </Table>
                        </Review>
                    ))}</>) : (<>
                      <p>  .</p>
                    </>)}
                    
                  </MyReviewLayout>
                </Tab.Pane>
              </Tab.Content>
            </Col>
          </Row>
        </Tab.Container>
    </Wrapper>
    </AllThing>
  )
}

export default MyPagePresenter;